import React, { useContext } from "react";
import "react-tabs/style/react-tabs.css";
import { SymbolItemContext } from "../contexts/SymbolItemContext";
import { LayerItem } from "./LayerItem";
import { SymbolPropertyEditing } from "./SymbolPropertiesEditing";

export const LayersPanel = () => {
  const {
    renderTree,
    inGallery,
    changeInGallery,
  } = useContext(SymbolItemContext);

  const layersToggle = () => {
    changeInGallery(!inGallery);
  };

  return (
    <div className="layerPanel" key="layersPanel">
      {renderTree ? (
        <LayerItem
          treeNode={renderTree}
          key="layerItem"
          level={0}
          path={[]}
        />
      ) : null}
      {
        <div className="addLayerBtn" onClick={layersToggle}>
          <span>Add Symbol Layer</span>
        </div>
      }
      {<SymbolPropertyEditing />}
    </div>
  );
};
