import React, { useEffect, useState, useContext } from "react";
import "react-tabs/style/react-tabs.css";
import { SymbolItemContext } from "../contexts/SymbolItemContext";
import { PreviewCanvas } from "./PreviewCanvas";
import CIMSymbol from "@arcgis/core/symbols/CIMSymbol";
import * as esriLang from "@arcgis/core/core/lang";

type ImageDataWithAnchor = {
  imageData: ImageData;
  anchorPosition: { x: number; y: number };
};

export const LayerItem = (props: any) => {
  const {
    symbolJSON,
    changeSymbolJSON,
    cimSymbolRasterizer,
    currentLayerPath,
    changeCurrentLayerPath,
    itemBeingDragged,
    updateItemBeingDragged,
  } = useContext(SymbolItemContext);
  const [layerSymbolImage, setLayerSymbolImage] = useState<
    ImageDataWithAnchor
  >();
  const [expanded, setExpanded] = useState(false);
  const [selected, setSelected] = useState(false);

  useEffect(() => {
    const cimSymbol = new CIMSymbol({
      data: { type: "CIMSymbolReference", symbol: props.treeNode.data },
    });

    const isStroke = props.treeNode.data.type === "CIMLineSymbol";

    //There's a bug with CIMSymbolRasterizer. Remove clone when the bug is fixed.
    (cimSymbolRasterizer as any)
      .rasterizeCIMSymbolAsync(cimSymbol.clone(), null, null, null, {
        targetSize: 35,
        style: isStroke ? "legend" : "preview",
      })
      .then((res: any) => {
        setLayerSymbolImage(res);
      });
  }, [props.treeNode, cimSymbolRasterizer]);

  //Is there a CSS way for this?
  useEffect(() => {
    setSelected(currentLayerPath.join() === props.path.join());
  }, [currentLayerPath, props.path]);

  const handleDelete = (event: React.MouseEvent) => {
    event.stopPropagation();
    let updatedSymbolJSON = { ...symbolJSON };
    updatedSymbolJSON.symbolLayers.splice(props.path[props.path.length - 1], 1);
    changeSymbolJSON(updatedSymbolJSON);
    if (props.path[1] === currentLayerPath[1]) {
      //check symbolLayer index
      changeCurrentLayerPath([]);
    }
    //TODO: handle other layer type
  };

  const handleDuplicate = (event: React.MouseEvent) => {
    event.stopPropagation();
    let updatedSymbolJSON = { ...symbolJSON };
    const index = props.path[props.path.length - 1];
    updatedSymbolJSON.symbolLayers.splice(index, 0, esriLang.clone(updatedSymbolJSON.symbolLayers[index]));
    changeSymbolJSON(updatedSymbolJSON);
    if (props.path[1] === currentLayerPath[1]) {
      //check symbolLayer index
      changeCurrentLayerPath([]);
    }
    //TODO: handle other layer type
  };

  const handleClick = (event: React.MouseEvent) => {
    event.stopPropagation();
    changeCurrentLayerPath(props.path);
  };

  const handleExpand = (event: React.MouseEvent) => {
    event.stopPropagation();
    setExpanded(!expanded);
  };

  const handleDragStart = (event: React.MouseEvent) => {
    setExpanded(false);
    updateItemBeingDragged(props.path);
  };

  const handleDrag = (event: React.MouseEvent) => {
    const target = event.target as HTMLDivElement;
    target.style.position = "absolute";
    target.style.left = -999 + "px";
    target.style.top = -999 + "px";
  };

  const handleDragEnd = (event: React.MouseEvent) => {
    updateItemBeingDragged(null);
    const target = event.target as HTMLDivElement;
    target.style.removeProperty("position");
    target.style.removeProperty("left");
    target.style.removeProperty("top");
  };

  const handleDragOver = (event: React.MouseEvent) => {
    event.preventDefault();
    (event.target as HTMLDivElement).classList.add("draggedOver");
  };

  const handleDragLeave = (event: React.MouseEvent) => {
    (event.target as HTMLDivElement).classList.remove("draggedOver");
  };

  const handleDrop = (dropZoneIndex: number) => {
    let updatedSymbolJSON = { ...symbolJSON };
    let targetingLayer = {
      ...updatedSymbolJSON.symbolLayers[
        itemBeingDragged[itemBeingDragged.length - 1]
      ],
    };
    updatedSymbolJSON.symbolLayers.splice(
      itemBeingDragged[itemBeingDragged.length - 1],
      1
    );
    if (dropZoneIndex <= itemBeingDragged[itemBeingDragged.length - 1]) {
      updatedSymbolJSON.symbolLayers.splice(dropZoneIndex, 0, targetingLayer);
    } else {
      updatedSymbolJSON.symbolLayers.splice(
        dropZoneIndex - 1,
        0,
        targetingLayer
      );
    }
    changeSymbolJSON(updatedSymbolJSON);
    updateItemBeingDragged(null);
    changeCurrentLayerPath([]);
  };

  //TO BE CLEANED UP!
  return props.treeNode.type === "CIMPointSymbol" ||
    props.treeNode.type === "CIMLineSymbol" ||
    props.treeNode.type === "CIMPolygonSymbol"
    ? props.treeNode.children.map((item: any, index: number) => {
        return (
          <LayerItem
            key={index}
            index={index}
            treeNode={item}
            level={props.level}
            path={props.path
              .slice(0, props.path.length - 1)
              .concat(item.key)
              .concat(index)}
            draggable={props.level === 0 ? true : false}
          />
        );
      })
    : [
        props.level === 0 && itemBeingDragged && props.index === 0 ? (
          <div
            key={"dropzonetop"}
            className="dropZone"
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
            onDrop={() => handleDrop(0)}
          ></div>
        ) : null,
        <div
          key={"main"}
          className={
            props.level === 0 &&
            itemBeingDragged &&
            itemBeingDragged.join() === props.path.join()
              ? "layerItem beingDragged"
              : "layerItem"
          }
          style={
            props.treeNode.children.length
              ? { paddingLeft: 10 * props.level + "px" }
              : { paddingLeft: 10 * props.level + 16 + "px" }
          }
          draggable={props.draggable}
          onDragStart={handleDragStart}
          onDrag={handleDrag}
          onDragEnd={handleDragEnd}
        >
          <div className="layerItemMain">
            {layerSymbolImage
              ? [
                  props.treeNode.children.length ? (
                    <span
                      key="arrow"
                      onClick={handleExpand}
                      className={expanded ? "arrow rotated" : "arrow"}
                    >
                      
                    </span>
                  ) : null,
                  <div className="preview" key="preview">
                    <PreviewCanvas
                      key="preview"
                      onClick={handleClick}
                      canvasWidth={layerSymbolImage.imageData.width || 50}
                      canvasHeight={layerSymbolImage.imageData.height || 50}
                      symbolImageData={{
                        imageData: layerSymbolImage.imageData,
                        anchorPosition: { x: 0, y: 0 },
                      }}
                    />
                  </div>,
                ]
              : null}
            {/* <div className="deleteLayerBtn"><span>Delete Layer</span></div> */}
            <span
              key="layerType"
              onClick={handleClick}
              className={selected ? "layerType selected" : "layerType"}
            >
              {props.treeNode.type}
            </span>
            {props.level === 0 ? (
              <div className="layerItemBtns">
                <div
                  className={"duplicateLayerBtn"}
                  key={"c"}
                  onClick={handleDuplicate}
                >
                  <span></span>
                </div>
                <div
                  className={"deleteLayerBtn"}
                  key={"x"}
                  onClick={handleDelete}
                >
                  <span>+</span>
                </div>
              </div>
            ) : null}
          </div>
          {expanded
            ? [
                (props.treeNode.children as Array<any>).map((c, index) => (
                  <LayerItem
                    key={index}
                    treeNode={c}
                    level={props.level + 1}
                    path={props.path.concat(c.key).concat(index)}
                  />
                )),
              ]
            : null}
        </div>,
        props.level === 0 &&
        itemBeingDragged &&
        itemBeingDragged.join() !== props.path.join() ? (
          <div
            key={"dropzonebottom"}
            className="dropZone"
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
            onDrop={() => handleDrop(props.index + 1)}
          ></div>
        ) : null,
      ];
};
