import Ajv from "ajv";

// esri.portal.schemas
import { json as cimSymbolReference } from "./schemas/cimSymbolReference";

const validator = new Ajv({ allErrors: true, extendRefs: true });
validator.addSchema(cimSymbolReference, "cimSymbolReference_schema.json");

export function validate(json: any): Ajv.ErrorObject[] | null | undefined {
  if (!validator.validate("cimSymbolReference_schema.json", json)) {
    return validator.errors;
  }
  return [];
}