import React, { Component } from "react";
import * as CIMSymbolRasterizer from "@arcgis/core/symbols/cim/CIMSymbolRasterizer";
import {
  CIMToRenderTree,
  getDefaultMarkerPlacement,
  getTemplate,
  symbolTypeToCIMSymbolType,
} from "../lib/utils/utils";
import {
  markerWithText,
  textCIMJSON,
  EsriPin,
  pictureMarker,
  defaultFill,
  defaultStroke,
} from "../data/CIMJSONs";
import FeatureLayer from "@arcgis/core/layers/FeatureLayer";

export type CustomResource = {
  type: "custom";
  items: Object[];
};

export type SymbolType = "point" | "line" | "polygon";

type PrimitivesResrouce = {
  type: "primitives";
  url: string;
};

type SymbolResourceItem = {
  name: string;
  key: string;
  resource: PrimitivesResrouce | CustomResource;
};

const presetSymbolResources: SymbolResourceItem[] = [
  {
    name: "Icons",
    key: "icons",
    resource: {
      type: "primitives",
      url:
        "https://jsapi.maps.arcgis.com/sharing/rest/content/items/5d9115dc254649b6bf6f0c975984ad59",
    },
  },
  {
    name: "Shapes",
    key: "shapes",
    resource: {
      type: "primitives",
      url:
        "https://jsapi.maps.arcgis.com/sharing/rest/content/items/5465f2caf4b04c51a636ed3bea20172e",
    },
  },
  {
    name: "Custom",
    key: "custom",
    resource: {
      type: "custom",
      items: [
        EsriPin,
        markerWithText,
        textCIMJSON,
        pictureMarker,
        defaultFill,
        defaultStroke,
      ],
    },
  },
];

export const SymbolItemContext = React.createContext<{
  item: any;
  symbolJSON: any;
  renderTree: any;
  currentSymbolType: SymbolType;
  currentLayer: any;
  currentLayerPath: any;
  tempLayers: any[];
  template: any;
  inGallery: boolean;
  symbolResources: SymbolResourceItem[];
  itemBeingDragged: any;
  cimSymbolRasterizer: any;
  pointLayer: FeatureLayer | null;
  lineLayer: FeatureLayer | null;
  polygonLayer: FeatureLayer | null;
  changePointLayer: Function;
  changeLineLayer: Function;
  changePolygonLayer: Function;
  changeSymbolJSON: Function;
  changeTempLayers: Function;
  updateSymbolJSONBasedOnNewValueOfCurrentLayer: Function;
  changeCurrentLayerPath: Function;
  changeCurrentSymbolType: Function;
  changeInGallery: Function;
  updateSymbolResources: Function;
  updateItemBeingDragged: Function;
}>({
  item: {}, //webstyleItem
  symbolJSON: {
    //curent symbolJSON for editting.
    type: "CIMPointSymbol",
    symbolLayers: [],
  },
  renderTree: null,
  currentLayer: {}, //current Layer reference being editted.
  currentSymbolType: "point",
  tempLayers: [], //new temp layers from gallery
  template: null, //for editing properties
  currentLayerPath: [],
  inGallery: false,
  symbolResources: [],
  itemBeingDragged: null,
  cimSymbolRasterizer: null,
  pointLayer: null,
  lineLayer: null,
  polygonLayer: null,
  changePointLayer: () => {},
  changeLineLayer: () => {},
  changePolygonLayer: () => {},
  changeSymbolJSON: () => {},
  changeTempLayers: () => {},
  updateSymbolJSONBasedOnNewValueOfCurrentLayer: () => {},
  changeCurrentLayerPath: () => {},
  changeCurrentSymbolType: () => {},
  changeInGallery: () => {},
  updateSymbolResources: () => {},
  updateItemBeingDragged: () => {},
});

class SymbolItemContextProvider extends Component {
  state = {
    item: {},
    symbolJSON: {
      type: "CIMPointSymbol",
      symbolLayers: [],
    },
    renderTree: null,
    currentLayer: {},
    currentLayerPath: [],
    currentSymbolType: "point" as SymbolType,
    tempLayers: [],
    template: null,
    inGallery: false,
    symbolResources: presetSymbolResources,
    itemBeingDragged: null,
    cimSymbolRasterizer: null,
    pointLayer: null,
    lineLayer: null,
    polygonLayer: null,
  };

  changePointLayer = (layer: FeatureLayer) => {
    this.setState({
      pointLayer: layer,
    });
  };

  changeLineLayer = (layer: FeatureLayer) => {
    this.setState({
      lineLayer: layer,
    });
  };

  changePolygonLayer = (layer: FeatureLayer) => {
    this.setState({
      polygonLayer: layer,
    });
  };

  changeSymbolJSON = (symbolJSON: any) => {
    this.setState({
      symbolJSON: symbolJSON,
      renderTree: CIMToRenderTree(symbolJSON),
    });
  };

  changeTempLayers = (layers: any[]) => {
    if (this.state.currentSymbolType === "point") {
      this.setState({
        tempLayers: layers,
      });
    } else {
      const isLine = this.state.currentSymbolType === "line";
      layers.forEach((layer) => {
        if (
          layer.type === "CIMVectorMarker" ||
          layer.type === "CIMPictureMarker"
        ) {
          layer.markerPlacement = isLine
            ? getDefaultMarkerPlacement("CIMMarkerPlacementAtExtremities")
            : getDefaultMarkerPlacement("CIMMarkerPlacementPolygonCenter");
        }
      });
      this.setState({
        tempLayers: layers,
      });
    }
  };

  changeCurrentLayerPath = (array: []) => {
    let currentLayer = this.state.symbolJSON;
    for (let i = 0; i < array.length; i++) {
      currentLayer = currentLayer[array[i]];
    }
    this.setState({
      currentLayerPath: array,
      currentLayer: currentLayer,
      template: getTemplate(currentLayer),
    });
  };

  changeCurrentSymbolType = (type: SymbolType) => {
    const newSymbolJSON = { ...this.state.symbolJSON };
    newSymbolJSON.type = symbolTypeToCIMSymbolType(type);
    if (type !== "point") {
      newSymbolJSON.symbolLayers.forEach((layer: any) => {
        if (
          layer.type === "CIMVectorMarker" ||
          layer.type === "CIMPictureMarker"
        ) {
          layer.markerPlacement = type === "line"
            ? getDefaultMarkerPlacement("CIMMarkerPlacementAtExtremities")
            : getDefaultMarkerPlacement("CIMMarkerPlacementPolygonCenter");
        }
      });
    }
    else{
      newSymbolJSON.symbolLayers.forEach((layer: any) => {
        if (
          layer.type === "CIMVectorMarker" ||
          layer.type === "CIMPictureMarker"
        ) {
          if(layer.markerPlacement){
            delete layer.markerPlacement
          }
        }
      });
    }
    this.setState({
      symbolJSON: newSymbolJSON,
      currentSymbolType: type,
    });
  };

  updateSymbolJSONBasedOnNewValueOfCurrentLayer = (
    propertyName: string,
    value: any
  ) => {
    const newSymbolJSON = { ...this.state.symbolJSON };
    let currentLayer: any = newSymbolJSON;
    for (let i = 0; i < this.state.currentLayerPath.length; i++) {
      currentLayer = currentLayer[this.state.currentLayerPath[i]];
    }
    currentLayer[propertyName] = value;
    this.changeSymbolJSON(newSymbolJSON);
  };

  changeInGallery = (g: any) => {
    this.setState({
      inGallery: g,
    });
  };

  updateSymbolResources = (symbolResources: SymbolResourceItem[]) => {
    this.setState({
      symbolResources,
    });
  };

  updateItemBeingDragged = (item: any) => {
    this.setState({
      itemBeingDragged: item,
    });
  };

  componentDidMount() {
    this.setState({
      cimSymbolRasterizer: new CIMSymbolRasterizer.CIMSymbolRasterizer(
        { wkid: 102100 },
        true
      ),
    });
  }

  render() {
    return (
      <SymbolItemContext.Provider
        value={{
          ...this.state,
          changeSymbolJSON: this.changeSymbolJSON,
          changeTempLayers: this.changeTempLayers,
          changePointLayer: this.changePointLayer,
          changeLineLayer: this.changeLineLayer,
          changePolygonLayer: this.changePolygonLayer,
          updateSymbolJSONBasedOnNewValueOfCurrentLayer: this
            .updateSymbolJSONBasedOnNewValueOfCurrentLayer,
          changeCurrentLayerPath: this.changeCurrentLayerPath,
          changeCurrentSymbolType: this.changeCurrentSymbolType,
          changeInGallery: this.changeInGallery,
          updateSymbolResources: this.updateSymbolResources,
          updateItemBeingDragged: this.updateItemBeingDragged,
        }}
      >
        {this.props.children}
      </SymbolItemContext.Provider>
    );
  }
}

export default SymbolItemContextProvider;
