import React, { useContext } from "react";
import "react-tabs/style/react-tabs.css";
import { SymbolPropertyItem } from "./SymbolPropertyItem";
import { SymbolItemContext } from "../contexts/SymbolItemContext";

export const SymbolPropertyEditing = () => {
  const {
    currentLayer,
    currentSymbolType,
    template,
    updateSymbolJSONBasedOnNewValueOfCurrentLayer,
  } = useContext(SymbolItemContext);
  return (
    <div className="SymbolPropertyEditing">
      <span>Layer Properties</span>
      {template && currentLayer
        ? template.map((input: any, index: number) => {
            const value =
              currentLayer[input.key] != null
                ? currentLayer[input.key]
                : input.default;
            return (
              <SymbolPropertyItem
                key={index}
                value={value}
                propertyName={input.key}
                keys={input.keys}
                inputType={input.type}
                name={input.name}
                unit={input.unit}
                onChange={updateSymbolJSONBasedOnNewValueOfCurrentLayer}
                symbolType={currentSymbolType}
                values={input.values}
              />
            );
          })
        : null}
    </div>
  );
};
