interface InputTemplate {
  name: string;
  key: string;
  type: string;
  default?: number | string | boolean | number[]

}

interface SingleSelectInputTemplate extends InputTemplate {
  type: "texts",
  values: string[],
  default?: string
}

interface NumberInputTemplate extends InputTemplate {
  type: "number"
  default?: number;
  unit?: string;
}

interface CheckBoxInputTemplate extends InputTemplate {
  type: "boolean",
  default?: boolean
}

interface NumbersInputTemplate extends InputTemplate {
  type: "numbers",
  default?: number[]
}

interface TextInputTemplate extends InputTemplate {
  type: "text"
}

interface URLInputTemplate extends InputTemplate {
  type: "imgURI"
}

//custom input
interface ColorInputTemplate {
  name: string,
  type: "Color",
  keys?: string[], //nested property
  key?: string
}

interface PointInputTemplate {
  name: string,
  type: "Point",
  key: string,
}

interface TextSymbolInputTemplate {
  name: string,
  type: "TextSymbol",
  key: string
}

interface AnchorPointInputTemplate {
  type: "AnchorPoint",
  name: string,
  key: string,
  inline: boolean,
  default: Object
}

interface MarkerPlacementInputTemplate {
  name: string,
  key: string,
  type: "MarkerPlacement"
}

type InputTemplateUnion = SingleSelectInputTemplate | NumberInputTemplate | CheckBoxInputTemplate | NumbersInputTemplate | TextInputTemplate | URLInputTemplate | ColorInputTemplate | PointInputTemplate | TextSymbolInputTemplate | AnchorPointInputTemplate | MarkerPlacementInputTemplate;

