import React, { useEffect } from "react";

export const PreviewCanvas = (props: any) => {
  const canvasRef = React.useRef<HTMLCanvasElement>(null);
  const canvasWidth = props.canvasWidth;
  const canvasHeight = props.canvasHeight;
  const symbolImageData = props.symbolImageData;

  useEffect(() => {
    if (canvasRef.current && symbolImageData) {
      const anchorPosition = symbolImageData.anchorPosition;
      const width = symbolImageData.imageData.width;
      const height = symbolImageData.imageData.height;

      const ctx = (canvasRef.current as HTMLCanvasElement).getContext("2d");
      ctx?.clearRect(0, 0, canvasWidth, canvasHeight);
      (ctx as CanvasRenderingContext2D).putImageData(
        symbolImageData.imageData,
        canvasWidth / 2 - width / 2 - width * anchorPosition.x,
        canvasHeight / 2 - height / 2 - height * anchorPosition.y
      );
    }
  });

  return <canvas onClick={props.onClick} ref={canvasRef} width={canvasWidth} height={canvasHeight} />;
};
