import React, { useEffect, useState, useContext, useRef } from "react";
import { PrimitiveThumbnail } from "./PrimitiveThumbnail";
import {
  SymbolItemContext,
  CustomResource,
} from "../contexts/SymbolItemContext";
import * as schemaValidator from "../schemaValidator";

export const PrimitiveGallery = (props: any) => {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [items, setItems] = useState([]);
  const [cimSymbolItems] = useState(props.cimSymbolItems);
  const [addingSymbol, setAddingSymbol] = useState(false);
  const [addingSymbolErrorMsg, setAddingSymbolErrorMsg] = useState<
    string | null
  >(null);
  const addSymbolItemRef = useRef<HTMLTextAreaElement>(null);
  const { symbolResources, updateSymbolResources } = useContext(
    SymbolItemContext
  );

  // Note: the empty deps array [] means
  // this useEffect will run once
  // similar to componentDidMount()
  useEffect(() => {
    if (props.url) {
      fetch(props.url + "/data")
        .then((res) => res.json())
        .then(
          (result) => {
            setIsLoaded(true);
            setItems(result.items);
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            setIsLoaded(false);
            setError(error);
          }
        );
    }
  }, [props.url]);

  const handleAddSymbol = () => {
    setAddingSymbolErrorMsg(null);
    setAddingSymbol(true);
  }; // svg/pms/cim

  const handleCommitAddItem = () => {
    setAddingSymbolErrorMsg(null);

    if (addSymbolItemRef.current) {
      const currentValue = addSymbolItemRef.current.value;
      let cimObject;
      try {
        cimObject = JSON.parse(currentValue);
      } catch (e) {
        setAddingSymbolErrorMsg("Not a Valid JSON");
        return;
      }

      if (
        cimObject.type !== "CIMPointSymbol" &&
        cimObject.type !== "CIMLineSymbol" &&
        cimObject.type !== "CIMPolygonSymbol"
      ) {
        setAddingSymbolErrorMsg("Currently, only CIMPointSymbol, CIMLineSymbol and CIMPolygonSymbol are supported");
        return;
      }

      const validationResult = schemaValidator.validate({
        type: "CIMSymbolReference",
        symbol: cimObject,
      });
      if (validationResult && validationResult.length) {
        console.log(validationResult);
        setAddingSymbolErrorMsg(
          "Not a valid CIMSymbol. " + validationResult.length + " errors found!"
        );
      } else {
        const newResources = [...symbolResources];

        for (const resource of newResources) {
          if (resource.key === props.pgKey) {
            (resource.resource as CustomResource).items.push(cimObject);
          }
        }

        updateSymbolResources(newResources);
        setAddingSymbol(false);
      }
    }

    //load from local
  };

  const handelCancelAddItem = () => {
    setAddingSymbol(false);
  };

  return (
    <div className="primitiveGallery">
      {error ? <div>Unable to fetch items.</div> : null}
      {isLoaded === true ? (
        items.map((item: any, key: number) => {
          return (
            <PrimitiveThumbnail key={key} item={item} cimStyleUrl={props.url} />
          );
        })
      ) : cimSymbolItems ? (
        addingSymbol ? (
          <div className="addItemOverlay">
            <span>Input a CIMSymbol JSON:</span>
            <textarea ref={addSymbolItemRef}></textarea>
            <div>{addingSymbolErrorMsg}</div>
            <div className="addItemOverlayer_buttons">
              <div onClick={handleCommitAddItem}>Add</div>
              <div onClick={handelCancelAddItem}>Cancel</div>
            </div>
          </div>
        ) : (
          cimSymbolItems.map((item: any, key: number) => {
            return <PrimitiveThumbnail key={key} cimSymbol={item} />;
          })
        )
      ) : null}
      {props.allowAddingSymbol && !addingSymbol ? (
        <div onClick={handleAddSymbol} className={"addSymbolBtn"}>
          +
        </div>
      ) : null}
    </div>
  );
};
