import CIMSymbol from "@arcgis/core/symbols/CIMSymbol";
import React, { useEffect, useState, useContext } from "react";
import { SymbolItemContext, SymbolType } from "../contexts/SymbolItemContext";
import { symbolTypeToCIMSymbolType } from "../lib/utils/utils";
import { PreviewCanvas } from "./PreviewCanvas";

export const SymbolPreview = (props: { symbolType: SymbolType }) => {
  const { symbolJSON, tempLayers, cimSymbolRasterizer } = useContext(
    SymbolItemContext
  );
  const [symbolImage, setSymbolImage] = useState();

  useEffect(() => {
    if (cimSymbolRasterizer !== null) {
      const cimSymbol = new CIMSymbol({
        data: { type: "CIMSymbolReference", symbol: symbolJSON },
      });
      (cimSymbolRasterizer as any)
        .rasterizeCIMSymbolAsync(cimSymbol, null, null, null, {
          style: "preview",
        })
        .then((value: any) => {
          setSymbolImage(value);
        });
    }
  }, [symbolJSON, cimSymbolRasterizer]);

  useEffect(() => {
    if (cimSymbolRasterizer !== null) {
      const cimSymbol = new CIMSymbol({
        data: {
          type: "CIMSymbolReference",
          symbol: {
            type: symbolTypeToCIMSymbolType(props.symbolType),
            symbolLayers: [...tempLayers, ...symbolJSON.symbolLayers],
          },
        },
      });
      (cimSymbolRasterizer as any)
        .rasterizeCIMSymbolAsync(cimSymbol, null, null, null, {
          style: "preview",
        })
        .then((value: any) => {
          setSymbolImage(value);
        });
    }
  }, [
    tempLayers,
    cimSymbolRasterizer,
    symbolJSON.symbolLayers,
    props.symbolType,
  ]);

  return (
    <div className="symbolPreview">
      <PreviewCanvas
        canvasWidth={300}
        canvasHeight={
          window.innerHeight * 0.2 > 120 ? window.innerHeight * 0.2 : 120
        }
        symbolImageData={symbolImage}
      />
    </div>
  );
};
