import CIMSymbol from "@arcgis/core/symbols/CIMSymbol";
import React, { useContext, useEffect, useState } from "react";
import { SymbolItemContext } from "../contexts/SymbolItemContext";
import * as esriLang from "@arcgis/core/core/lang";

export const PrimitiveThumbnail = (props: any) => {
  const { changeTempLayers, cimSymbolRasterizer} = useContext(
    SymbolItemContext
  );

  const [previewImageUrl, setPreviewImageUrl] = useState<string>();

  function thumbnailClickHandler() {
    fetch(props.cimStyleUrl + props.item.cimRef.slice(1))
      .then((res) => res.json())
      .then(
        (result) => {
          changeTempLayers(result.symbolLayers);
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        () => {}
      );
  }

  function cimSymbolPreviewClickHandler() {
    //deep clone
    changeTempLayers(esriLang.clone(props.cimSymbol.symbolLayers)); //assume it is a CIMSymbolJSON
  }

  useEffect(() => {
    if (props.cimSymbol) {
      const cimSymbol = new CIMSymbol({
        data: { type: "CIMSymbolReference", symbol: props.cimSymbol },
      });

      //There's a bug with CIMSymbolRasterizer. Remove clone when the bug is fixed.
      (cimSymbolRasterizer as any)
        .rasterizeCIMSymbolAsync(cimSymbol.clone(), null, null, null, {
          targetSize: 35,
          style: "preview",
        })
        .then((res: any) => {
          const canvas = document.createElement("canvas");
          canvas.width = res.imageData.width;
          canvas.height = res.imageData.height;
          const ctx = canvas.getContext("2d");
          ctx?.putImageData(res.imageData, 0, 0);

          setPreviewImageUrl(canvas.toDataURL());
        });
    }
  }, [props.cimSymbol, cimSymbolRasterizer]);

  return props.cimStyleUrl && props.item ? (
    <div className="primitiveThumbnail">
      <img
        alt={"thumbnail"}
        onClick={thumbnailClickHandler}
        src={props.cimStyleUrl + props.item.thumbnail.href.slice(1)}
      ></img>
    </div>
  ) : props.cimSymbol ? (
    <div className="primitiveThumbnail" onClick={cimSymbolPreviewClickHandler}>
      <img
        alt={"thumbnail"}
        src={previewImageUrl}
      ></img>
    </div>
  ) : null;
};
