import React, { useEffect, useRef, RefObject, useContext } from "react";
import { SymbolItemContext } from "../contexts/SymbolItemContext";

import ArcGISMap from "@arcgis/core/Map";
import FeatureLayer from "@arcgis/core/layers/FeatureLayer";
import MapView from "@arcgis/core/views/MapView";

export const WebMapView = () => {
  const mapRef = useRef<HTMLDivElement>();
  const { changePointLayer, changeLineLayer, changePolygonLayer } = useContext(SymbolItemContext);

  useEffect(() => {
    const line = new FeatureLayer({
      portalItem: {
        id: "9a61ba12e9904388a0d17c48ce6a5662",
      },
      renderer: {
        type: "simple",
        symbol: {
          type: "simple-line",
        },
      } as any,
    });

    const polygon = new FeatureLayer({
      url:
        "https://services.arcgis.com/V6ZHFr6zdgNZuVG0/arcgis/rest/services/Lake_Tahoe/FeatureServer",
      renderer: {
        type: "simple",
        symbol: {
          type: "simple-fill",
        },
      } as any,
    });

    const point = new FeatureLayer({
      url:
        "https://services.arcgis.com/V6ZHFr6zdgNZuVG0/arcgis/rest/services/TahoeTrailheads/FeatureServer",
      renderer: {
        type: "simple",
        symbol: {
          type: "simple-marker",
        },
      } as any,
    });

    const map = new ArcGISMap({
      layers: [point, line, polygon],
      basemap: "gray",
    });

    // load the map view at the ref's DOM node
    const view = new MapView({
      container: mapRef.current,
      map: map,
      center: [-120, 39.05],
      zoom: 10,
    });

    //const configureWidget

    changePointLayer(point);
    changeLineLayer(line);
    changePolygonLayer(polygon);

    return () => {
      if (view) {
        // destroy the map view
        view.container = null as any;
      }
    };
  }, []);

  return <div className="webmap" ref={mapRef as RefObject<HTMLDivElement>} />;
};
