import * as templates from "../../data/templates"
import { SymbolType } from "../../contexts/SymbolItemContext"

export function getTemplate(layer: any) {
  switch (layer.type) {
    case "CIMVectorMarker":
      return templates.VectorMarkerTemplate;
    case "CIMPictureMarker":
      return templates.PictureMarkerTemplate;
    case "CIMMarkerGraphic":
      if (layer.symbol.type === "CIMTextSymbol") {
        return templates.TextMarkerGraphicTemplate;
      }
      return templates.MarkerGraphicTemplate;
    case "CIMSolidFill":
      return templates.SolidFillTemplate;
    case "CIMSolidStroke":
      return templates.SolidStrokeTemplate;
    default:
      return;
  }
}

export function getMarkerPlacementTemplate(mp: string) {
  return (templates as any)[mp + "Template"];
}

export function hexToRgb(hex: string) {
  var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
  return result ? {
    r: parseInt(result[1], 16),
    g: parseInt(result[2], 16),
    b: parseInt(result[3], 16)
  } : null;
}

export function rgbToHex(rgb: { r: number, g: number, b: number }) {
  return "#" + ((1 << 24) + (rgb.r << 16) + (rgb.g << 8) + rgb.b).toString(16).slice(1);
}

export function rgbToCIMColor(rgb: { r: number, g: number, b: number } | null) {
  if (!rgb) {
    return null;
  }
  return [rgb.r, rgb.g, rgb.b, 255];
}

export function CIMColorToRgb(cimColor: number[]) {
  return { r: cimColor[0], g: cimColor[1], b: cimColor[2] }
}

export function CIMColorToHex(cimColor: number[]): string {
  return rgbToHex(CIMColorToRgb(cimColor));
}

export function CIMToRenderTree(CIMSymbolJSON: any): any {
  return analyzeMultiLayerSymbol(CIMSymbolJSON);
}

function analyzeMultiLayerSymbol(CIMSymbolJSON: any): any {
  let head;
  if (CIMSymbolJSON && CIMSymbolJSON.symbolLayers.length > 0) {
    head = {
      type: CIMSymbolJSON.type,
      key: "symbol",
      data: { ...CIMSymbolJSON },
      children: [] as any[]
    };
    for (const layer of CIMSymbolJSON.symbolLayers) {
      head.children.push(analyzeSymbolLayer(layer));
    }
  }
  return head;
}

function analyzeSymbolLayer(symbolLayer: any): any {
  let head;
  if (symbolLayer?.type) {
    head = {
      type: symbolLayer.type,
      key: "symbolLayers",
      data: null as any,
      children: [] as any[]
    }
    if (symbolLayer.type === "CIMVectorMarker" && symbolLayer.markerGraphics.length > 0) {
      head.data = {
        type: "CIMPointSymbol",
        symbolLayers: [{ ...symbolLayer, offsetX: 0, offsetY: 0, anchorPoint: { x: 0, y: 0 }, enable: true }]
      };

      let clonedMarkerGraphics = [];
      for (let i = 0; i < symbolLayer.markerGraphics.length; i++) {
        let markerGraphic = symbolLayer.markerGraphics[i];
        if (markerGraphic.symbol.type === "CIMTextSymbol") {
          markerGraphic = { ...markerGraphic, geometry: { x: 0, y: 0 }, symbol: { ...markerGraphic.symbol, offsetX: 0, offsetY: 0 } };
          clonedMarkerGraphics.push(markerGraphic);
        }
        else {
          clonedMarkerGraphics.push({ ...markerGraphic })
        }
        head.children.push(analyzeMarkerGraphic(markerGraphic, head.data.symbolLayers[0]));
      }
      head.data.symbolLayers[0].markerGraphics = clonedMarkerGraphics;
    }
    else if (symbolLayer.type === "CIMPictureMarker") {
      head.data = {
        type: "CIMPointSymbol",
        symbolLayers: [symbolLayer]
      }
    }
    else if (symbolLayer.type === "CIMSolidStroke") {
      head.data = {
        type: "CIMLineSymbol",
        symbolLayers: [symbolLayer]
      };
    }
    else if (symbolLayer.type === "CIMSolidFill" || symbolLayer.type === "CIMGradientFill") {
      head.data = {
        type: "CIMPolygonSymbol",
        symbolLayers: [symbolLayer]
      };
    }

    //TODO: other layer types
  }

  return head;
}

function analyzeMarkerGraphic(markerGraphic: any, vectorMarker: any): any {
  let head;
  if (markerGraphic) {
    head = {
      type: markerGraphic.type,
      key: "markerGraphics",
      data: {
        type: "CIMPointSymbol",
        symbolLayers: [{
          ...vectorMarker,
          markerGraphics: [markerGraphic]
        }]
      },
      children: [] as any[]
    }

    if (markerGraphic.symbol.type === "CIMPointSymbol" || markerGraphic.symbol.type === "CIMLineSymbol" || markerGraphic.symbol.type === "CIMPolygonSymbol") {
      head.children.push(analyzeMultiLayerSymbol(markerGraphic.symbol))
    }
  }

  return head;
}

export function isImgDataLink(value: string): boolean {
  const dataUrlRegex = /^data:(.*?)(;base64)?,(.*)$/;
  const result = value.match(dataUrlRegex);
  if (!result) {
    return false;
  }
  if (result[1].toLocaleLowerCase() === "image/png" || result[1].toLocaleLowerCase() === "image/jpeg") {
    return true;
  }
  return false;
}

export function isURL(value: string): boolean {
  const dataUrlRegex = /^(?:http(s)?:\/\/)?[\w.-]+(?:\.[\w.-]+)+[\w\-._~:/?#[\]@!$&'()*+,;=.]+$/;
  const result = value.match(dataUrlRegex);
  if (!result) {
    return false;
  }
  return true;
}

export function symbolTypeToCIMSymbolType(symbolType: SymbolType): "CIMPointSymbol" | "CIMLineSymbol" | "CIMPolygonSymbol" {
  switch (symbolType) {
    case "point":
      return "CIMPointSymbol";
    case "line":
      return "CIMLineSymbol";
    case "polygon":
      return "CIMPolygonSymbol"
    default:
      return "CIMPolygonSymbol"
  }
}

export function getDefaultMarkerPlacement(type: string) {
  const template = getMarkerPlacementTemplate(type);
  const result: any = {
    type
  }

  template.forEach((item: any)=>{
    if(item.default != null){
      result[item.key] = item.default
    }
  })
  return result;
}