import React, { useEffect, useState, useContext, useRef } from "react";
import { Tabs, TabList, Tab, TabPanel } from "react-tabs";
import { PrimitiveGallery } from "./PrimitiveGallery";
import { SymbolItemContext } from "../contexts/SymbolItemContext";
import { isURL } from "../lib/utils/utils";

export const SymbolGallery = () => {
  const { symbolResources, updateSymbolResources } = useContext(
    SymbolItemContext
  );
  const [tabs, setTabs] = useState<JSX.Element[]>();
  const [tabPanels, setTabPanels] = useState<JSX.Element[]>();
  const addWebStyleItemRef = useRef<HTMLInputElement>(null);
  const [initializeWebStyleError, setInitializeWebStyleError] = useState<String | null>(null);

  useEffect(() => {
    let currentTabs: JSX.Element[] = [];
    let currentTabPanels: JSX.Element[] = [];

    symbolResources.forEach((resource, index) => {
      currentTabs.push(<Tab key={index}>{resource.name}</Tab>);
      if (resource.resource.type === "primitives") {
        currentTabPanels.push(
          <TabPanel key={index}>
            <PrimitiveGallery
              url={resource.resource.url}
              allowAddingSymbol={false}
              pgKey={resource.key}
            />
          </TabPanel>
        );
      } else if (resource.resource.type === "custom") {
        currentTabPanels.push(
          <TabPanel key={index}>
            <PrimitiveGallery
              cimSymbolItems={resource.resource.items}
              allowAddingSymbol={true}
              pgKey={resource.key}
            />
          </TabPanel>
        );
      }
    });

    setTabs(currentTabs);
    setTabPanels(currentTabPanels);
  }, [symbolResources]);

  const initializeWebStyleHandler = () => {
    setInitializeWebStyleError(null);
    if (addWebStyleItemRef && addWebStyleItemRef.current) {
      if (!isURL(addWebStyleItemRef.current.value)) {
        setInitializeWebStyleError("Invalid URL");
        return;
      }
      const newSymbolResources = [
        ...symbolResources,
        {
          name: "WebStyle",
          key: "webstyle" + symbolResources.length,
          resource: {
            type: "primitives",
            url: addWebStyleItemRef.current.value,
          },
        },
      ];
      addWebStyleItemRef.current.value = "";
      updateSymbolResources(newSymbolResources);
    }
  };

  return (
    <div className="SymbolGallery" key="symbolGallery">
      <Tabs forceRenderTabPanel={true}>
        <TabList>
          {tabs}
          <Tab key="+">+</Tab>
        </TabList>
        {tabPanels}
        <TabPanel key="empty">
          {/*add a url in sources*/}
          <div className="addWebStyle">
            <span>Web Style URL:</span>
            <span>e.g. https://www.arcgis.com/sharing/rest/content/items/220936cc6ed342c9937abd8f180e7d1e</span>
            <input ref={addWebStyleItemRef} type="text" />
            {initializeWebStyleError ? <span>{initializeWebStyleError}</span> : null}
            <div>
              <div onClick={initializeWebStyleHandler}>Add New Style</div>
            </div>
          </div>
        </TabPanel>
      </Tabs>
    </div>
  );
};
